import { Component , OnInit , ViewChild , HostBinding } from '@angular/core';
import {AlertController, ModalController ,  IonRouterOutlet} from '@ionic/angular';
import { TranslateService } from '@ngx-translate/core';
import {  Router , NavigationExtras } from '@angular/router';
import { AuthenticationService } from '../../services/Authentication.service';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import {Location} from '@angular/common';


import { ReplaySubject} from 'rxjs';

import { BehaviorSubject } from 'rxjs';

import { MyModalcardPage } from '../../../DESARROLLO/modals/my-modalcard/my-modalcard.page';




@Component({
  selector: 'app-producto-detalle1',
  templateUrl: './producto-detalle1.page.html',
  styleUrls: ['./producto-detalle1.page.scss'],
})
export class ProductoDetalle1Page implements OnInit {

  showmenu = false;
  llave:any;
  details:any=[];
  cartItemCount: BehaviorSubject<number>;
    items: any  = [] ;
    variables: any = [];
    userinfo: any = [];
    viewType = 'grid';
    fotos: any= []
    tipovista:any;
  moneda:any;
    tituloxx = '';
    efecto = 0;
    seguro = '';
    sifoto=0;
    sienvio:any = false;
    ratingForm: FormGroup;
    msg = '';
    apifoto : String;
    itemsList: Array<any>;
    colorVariants = [];
    sizeVariants = [];
    parametro : any;
    slidesOptions: any = {
      zoom: {
        toggle: false // Disable zooming to prevent weird double tap zomming on slide images
      }
    };
  
    @HostBinding('class.is-shell') get isShell() {
      return (this.details && this.details.isShell) ? true : false;
    }
  
  
    filterList2(list, op): Array<any> {
      return list.filter(item => item.id === op);
    }
  
  
  constructor(  private location: Location, private routerOutlet: IonRouterOutlet , private router: Router, private authenticationService: AuthenticationService,
     public alertController: AlertController,     public translateService: TranslateService    ,    private modalController: ModalController
     ) { this.apifoto = this.authenticationService.getapifoto();

      
    this.ratingForm = new FormGroup({
      rate1: new FormControl( '',Validators.required),
      rate2: new FormControl('', Validators.required),
      rate3: new FormControl('', Validators.required),
      rate4: new FormControl('', Validators.required),
      rate5: new FormControl('', Validators.required)

     
    });

   }
  
    ngOnInit() {
      this.variables = this.router.getCurrentNavigation().extras.state.variables;
      this.llave = this.router.getCurrentNavigation().extras.state.llave0;
      this.sienvio = this.router.getCurrentNavigation().extras.state.sienvio;
      this.cartItemCount = this.authenticationService.getCartItemCount();
    
      this.tipovista = this.router.getCurrentNavigation().extras.state.tipovista;
      if(  this.tipovista.indexOf('con imagenes')>0)
      {
        this.sifoto=1;
      }

      this.moneda = this.router.getCurrentNavigation().extras.state.moneda;
      this.userinfo = this.authenticationService.getcopiauser();
      this.tituloxx =   this.router.getCurrentNavigation().extras.state.titulo ;

      this.translateService.get('SEGUROM').subscribe((value) => {   this.seguro = value  });
      this.translateService.get('MSGM').subscribe((value) => {   this.msg = value  });


      this.details = this.router.getCurrentNavigation().extras.state.data;

      this.fotos.push({type: "square", source:this.apifoto  + this.details.foto1})
      this.fotos.push({type: "fill", source:this.apifoto  + this.details.foto2})
      this.fotos.push({type: "fill", source:this.apifoto  + this.details.foto3})

      // this.itemsList = this.filterList2(this.details, this.parametro);
/*
       this.colorVariants = this.details.colorVariants
       .map(item =>
         ({
           name: item.name,
           type: 'radio',
           label: item.name,
           value: item.value,
           checked: item.default
         })
       );
       this.sizeVariants = this.details.sizeVariants
       .map(item =>
         ({
           name: item.name,
           type: 'radio',
           label: item.name,
           value: item.value,
           checked: item.default
         })
       );
*/
   
    }
  
    async openColorChooser() {
      const alert = await this.alertController.create({
        header: 'Colores',
        inputs: this.colorVariants,
        cssClass: 'variant-alert color-chooser',
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
            handler: () => {
              console.log('Confirm Cancel');
            }
          }, {
            text: 'OK',
            handler: () => {
              console.log('Confirm Ok');
            }
          }
        ]
      });
  
      await alert.present();
    }
  
    async openSizeChooser() {
      const alert = await this.alertController.create({
        header: 'Tamaños',
        inputs: this.sizeVariants,
        cssClass: 'variant-alert size-chooser',
        buttons: [
          {
            text: 'Cancel',
            role: 'cancel',
            cssClass: 'secondary',
            handler: () => {
              console.log('Confirm Cancel');
            }
          }, {
            text: 'OK',
            handler: (selectedValue) => {
              console.log('Selected Value', selectedValue);
            }
          }
        ]
      });
  
      await alert.present();
    }


    

  masprod(item, event: any) {
   
    this.authenticationService.addProduct({idproveedor: this.llave ,id: item._id,  name: item.nombre + ' ' + item.xunidad + ' ' + item.unidad,  price: item.precio,  amount: 1,rate:this.ratingForm.controls['rate1'].value + '°' +
    this.ratingForm.controls['rate2'].value + '°' +this.ratingForm.controls['rate3'].value + '°' +this.ratingForm.controls['rate4'].value + '°' +this.ratingForm.controls['rate5'].value});

    this.location.back();
}



async openCart() {
  const modal = await this.modalController.create({
    component: MyModalcardPage,
    animated: true,
    componentProps:  {sienvio:this.sienvio ,moneda:this.moneda, nombre:  this.userinfo[0].nombre , direccion: this.userinfo[0].direccion,  telefono: this.userinfo[0].telefono, correo: this.userinfo[0].correo   },
   
    presentingElement: await this.modalController.getTop() , cssClass: 'modal-fullscreen'
  });
  modal.onWillDismiss().then((data) => {
  });
  return await modal.present();
}


}
